.button {
    background-color: black; 
    border: 5px;
    color: white;
    padding: 10px 20px;   
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 0px 0px 30px ;
    transition-duration: 0.4s;
    cursor: pointer;
    background-color: white; 
    color: black; 
    border: 2px solid rgb(72, 92, 155);
    position: relative;
    margin-left: auto;
    margin-right: auto;
    width: 45px;
    height: 45px;
    color: black;
    font-weight: bold;
    border-radius: 5px;
    text-align: center;

    
    
  }

  
  .button:hover  {
    background-color: rgb(191, 205, 250);
    box-shadow: 0 5px 6px -2px #777;
  }

  i{
    color:rgb(72, 92, 155);
    font-weight: bold;
    margin: 0px -20px;
}

