@media  (max-width: 1400px) {

    .dataBlock{
        margin:0px 6%;
    }

    .dataBody{
      width: 830px;
      margin: 0 15%;
      text-align: center;
    }

    .headerOFdataTable{
        margin: 50px 0% -59px -290px;
        font-size: 23px;
    }

  }


@media  (max-width: 850px) {
    .siteName{
        position: relative;
        left:-2%;
    }

    .dataBlock{
